import React, { useEffect, useRef } from "react";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Button from "react-bootstrap/Button";
import Navbar from "react-bootstrap/Navbar";
import Nav from "react-bootstrap/Nav";
import i18n from "../languages/i18n";
import { useTranslation } from "react-i18next";

import Color from "../config/colors";

function Menu({ handleScr }) {
  const { t, i18n } = useTranslation();
  const lanRef = useRef([]);

  const handlelang = (lan, idx) => {
    i18n.changeLanguage(lan);
    for (let i = 0; i < 5; i++) {
      lanRef.current[i].style.borderBottom = "blue solid 0px";
    }
    lanRef.current[idx].style.borderBottom = "yellow solid 2px";
  };

  useEffect(() => {
    handlelang("it", 0);
  }, []);

  return (
    <Container fluid className="d-flex justify-content-between navbar">
      <div>
        <div className="navtitle">Scritti di Comboni</div>
        <div className="navitem" onClick={() => handleScr(0)}>
          Paragraph
        </div>
        <div className="navitem" onClick={() => handleScr(1)}>
          Letter
        </div>
        <div className="navitem" onClick={() => handleScr(2)}>
          Number
        </div>
        <div className="navitem" onClick={() => handleScr(3)}>
          Bio
        </div>
        <div className="navitem" onClick={() => handleScr(4)}>
          Cloud
        </div>
        <div className="navitem" onClick={() => handleScr(5)}>
          Origin
        </div>
        <div className="navitem" onClick={() => handleScr(6)}>
          {/* Timeline */}
        </div>
      </div>
      <div>
        <div
          ref={(el) => (lanRef.current[0] = el)}
          className="iconlan"
          onClick={() => handlelang("it", 0)}
        >
          IT
        </div>
        <div
          ref={(el) => (lanRef.current[1] = el)}
          className="iconlan"
          onClick={() => handlelang("en", 1)}
        >
          EN
        </div>
        <div
          ref={(el) => (lanRef.current[2] = el)}
          className="iconlan"
          onClick={() => handlelang("es", 2)}
        >
          ES
        </div>
        <div
          ref={(el) => (lanRef.current[3] = el)}
          className="iconlan"
          onClick={() => handlelang("fr", 3)}
        >
          FR
        </div>
        <div
          style={{ marginRight: 20 }}
          ref={(el) => (lanRef.current[4] = el)}
          className="iconlan"
          onClick={() => handlelang("pt", 4)}
        >
          PT
        </div>
      </div>
    </Container>
  );
}

export default Menu;
