import React from "react";
import Highlighter from "react-highlight-words";
import { useTranslation } from "react-i18next";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";

function SearchResults({ respar, arrayText, handleLetterDetail }) {
  const Letters = require("../json/letters.json");
  const { t, i18n } = useTranslation();

  if (respar) {
    respar.map((item) => {
      const resultado = Letters.find((lett) => lett.id === item.letter);
      item.to = resultado.to;
      item.from = resultado.from;
      item.date = resultado.date;
      item.subject = resultado.subject;
    });
  } else console.log("Query not found");

  return (
    <div>
      {respar.length > 0 ? (
        <div className="result_box">
          {t("results")}: {respar.length}
        </div>
      ) : (
        <div></div>
      )}
      <hr />
      {respar.map((item, index) => (
        <div key={index}>
          <Row>
            <Col sm={4}>
              <div>
                <div className="d-flex justify-content-between">
                  <div>{item.from}</div>
                  <div>{item.date}</div>
                </div>
              </div>
              <div>
                <b>{item.to}</b>
              </div>
              marginRight: 10
              <div>{item.subject}</div>
              <div
                onClick={() => handleLetterDetail(item.letter)}
                className="gotoletterbox"
              >
                {t("see_all_letter")}
              </div>
            </Col>
            <Col sm={8}>
              <Highlighter
                highlightClassName="highlight"
                searchWords={arrayText}
                autoEscape={true}
                textToHighlight={item.par}
              />
            </Col>
          </Row>
          <hr />
        </div>
      ))}
    </div>
  );
}

export default SearchResults;
