import React from "react";
import Modal from "react-bootstrap/Modal";
import Button from "react-bootstrap/Button";
import { useTranslation } from "react-i18next";

function LetterDetail({ handleCloseModal, letDetail }) {
  const { t, i18n } = useTranslation();

  const lan = i18n.language;
  const Letters = require("../json/letters.json");
  var Scritti = require("../json/par_it.json");
  if (lan == "es") {
    Scritti = require("../json/par_es.json");
  } else if (lan == "en") {
    Scritti = require("../json/par_en.json");
  } else if (lan == "pt") {
    Scritti = require("../json/par_pt.json");
  } else if (lan == "fr") {
    Scritti = require("../json/par_fr.json");
  }

  const letterdata = Letters.filter((item) => item.id == letDetail).map(
    ({ id, to, subject, from, date }) => ({
      id,
      to,
      subject,
      from,
      date,
    })
  );

  const paragraghs = Scritti.filter((item) => item.letter == letDetail).map(
    ({ id, letter, par }) => ({ id, letter, par })
  );

  console.log(letterdata);
  console.log(paragraghs);

  return (
    <div>
      <Modal.Header closeButton>
        <Modal.Title>
          <div>{letterdata[0].to}</div>
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <div style={{ fontStyle: "italic" }}>{letterdata[0].subject}</div>
        <div className="d-flex justify-content-between modalletdetailhed2">
          <div>{letterdata[0].from}</div>
          <div>{letterdata[0].date}</div>
        </div>
        {paragraghs.map((item, index) => (
          <div key={index}>
            <div style={{ marginTop: 20 }}>{item.par}</div>
          </div>
        ))}
      </Modal.Body>
      <Modal.Footer>
        <Button variant="secondary" onClick={handleCloseModal}>
          Close
        </Button>
      </Modal.Footer>
    </div>
  );
}

export default LetterDetail;
