import React, { useState, useEffect } from "react";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import { useTranslation } from "react-i18next";

function SearchNumber(props) {
  const { t, i18n } = useTranslation();
  const lan = i18n.language;
  const Letters = require("../json/letters.json");
  var Scritti = require("../json/par_it.json");
  if (lan == "es") {
    Scritti = require("../json/par_es.json");
  } else if (lan == "en") {
    Scritti = require("../json/par_en.json");
  } else if (lan == "pt") {
    Scritti = require("../json/par_pt.json");
  } else if (lan == "fr") {
    Scritti = require("../json/par_fr.json");
  }

  const [parNum, setParNum] = useState(1);
  const [letNum, setLetNum] = useState(1);

  const handlePrev = () => {
    if (parNum > 1) {
      setParNum(parNum - 1);
    }
  };

  const handleNext = () => {
    if (parNum < 7257) {
      setParNum(parNum + 1);
    }
  };

  if (parNum > 7257) {
    setParNum(7257);
  }

  const getLetNum = () => {
    var myPar = Scritti.filter((item) => item.id === parNum);
    var myLet = Letters.filter(
      (item) =>
        item.id === Scritti.filter((item) => item.id === parNum)[0].letter
    );
    return myLet;
  };

  const Header = () => {
    var myPar = Scritti.filter((item) => item.id === parNum);
    var myLet = Letters.filter(
      (item) =>
        item.id === Scritti.filter((item) => item.id === parNum)[0].letter
    )[0];

    return (
      <div>
        <div>
          {myLet.from}, {myLet.date}
        </div>
        <div>
          <b>{myLet.to}</b>
        </div>
        <div>
          <i>{myLet.subject}</i>
        </div>
      </div>
    );
  };

  const Item = () => {
    return (
      <>
        {Scritti.filter((par) => par.id === parNum).map((item, index) => (
          <div key={index}>{item.par}</div>
        ))}
      </>
    );
  };

  return (
    <>
      <Row style={{ marginTop: 20 }} className="justify-content-center">
        <Col sm={1}>
          <Button
            variant="default"
            style={{
              marginRight: 10,
              marginTop: -5,
              fontSize: 18,
              display: "inline-block",
              backgroundColor: "#626556",
              color: "white",
            }}
            onClick={() => handlePrev()}
          >
            Previous
          </Button>
        </Col>
        <Col sm={1}>
          <Form.Group className="mb-3" controlId="f1">
            <Form.Control
              name="search by number"
              type="number"
              value={parNum}
              required
              placeholder="number"
              onChange={(e) => setParNum(parseInt(e.target.value, 10))}
              size="sm"
            />
          </Form.Group>
        </Col>
        <Col sm={1}>
          <Button
            variant="default"
            style={{
              marginRight: 10,
              marginTop: -5,
              fontSize: 18,
              display: "inline-block",
              backgroundColor: "#626556",
              color: "white",
            }}
            onClick={() => handleNext()}
          >
            Next
          </Button>
        </Col>
      </Row>
      <hr />
      <Row>
        <Col sm={4}>
          <Header />
        </Col>
        <Col sm={8}>
          <Item />
        </Col>
      </Row>
    </>
  );
}

export default SearchNumber;
