import { useEffect } from "react";
import PropTypes from "prop-types";
import { useMap } from "react-leaflet";
import L from "leaflet";
import "leaflet.polyline.snakeanim/L.Polyline.SnakeAnim.js";
import icon from "./constants";

const SnakeAnim = ({ startAnimation }) => {
  const map = useMap();

  useEffect(() => {
    if (!startAnimation) return;
    const trd = [63.5, 11];
    const mad = [40.5, -3.5];
    const lnd = [51.5, -0.5];
    const ams = [52.3, 4.75];
    const vlc = [39.5, -0.5];

    const route = L.featureGroup(
      [
        L.marker(trd, { icon }),
        L.polyline([trd, ams], { snakingSpeed: 50 }),
        L.marker(ams, { icon }),
        L.polyline([ams, lnd]),
        L.marker(lnd, { icon }),
        L.polyline([lnd, mad]),
        L.marker(mad, { icon }),
        L.polyline([mad, vlc]),
        L.marker(vlc, { icon }),
      ],
      { snakingPause: 1000 }
    );

    map.fitBounds(route.getBounds());

    map.addLayer(route);

    route.snakeIn();

    route.on("snakestart snake snakeend", (ev) => {
      console.log(ev.type);
    });
    console.log(map);
  }, [startAnimation]);

  return null;
};

SnakeAnim.propTypes = {
  startAnimation: PropTypes.bool.isRequired,
};

export default SnakeAnim;
