import React from "react";
import Highlighter from "react-highlight-words";
import { useTranslation } from "react-i18next";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";

function SearchLetterResults({ respar, handleLetterDetail }) {
  const { t, i18n } = useTranslation();
  const Letters = require("../json/letters.json");

  console.log(respar);

  return (
    <div>
      <Row>
        <Col sm={4} align="right" style={{ marginTop: 5 }}>
          {respar.length > 0 ? (
            <div className="result_box" style={{ marginTop: 10 }}>
              {t("results")}: {respar.length}
            </div>
          ) : (
            <div></div>
          )}
        </Col>
        <Col sm={8}>
          <hr />
          {respar.map((item, index) => (
            <div key={index}>
              <div class="d-flex justify-content-between">
                <div>
                  {item.from}, {item.date}
                </div>
                <div
                  onClick={() => handleLetterDetail(item.id)}
                  className="gotoletterbox"
                >
                  {t("see_all_letter")}
                </div>
              </div>
              <div>
                <b>{item.to}</b>
              </div>
              <div>
                <i>{item.subject}</i>
              </div>

              <hr />
            </div>
          ))}
        </Col>
      </Row>
    </div>
  );
}

export default SearchLetterResults;
