import React, { useState, useEffect } from "react";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import Modal from "react-bootstrap/Modal";
import { useTranslation } from "react-i18next";

import SearchResults from "./SearchResults";
import LetterDetail from "./LetterDetail";

function Search() {
  const { t, i18n } = useTranslation();
  const [respar, setRespar] = useState([]);
  const [arrayText, setArrayText] = useState([]);
  const [text, setText] = useState("");
  const [showModal, setShowModal] = useState(false);
  const [letDetail, setLetDetail] = useState();

  const lan = i18n.language;

  const Letters = require("../json/letters.json");
  var Scritti = require("../json/par_it.json");
  if (lan == "es") {
    Scritti = require("../json/par_es.json");
  } else if (lan == "en") {
    Scritti = require("../json/par_en.json");
  } else if (lan == "pt") {
    Scritti = require("../json/par_pt.json");
  } else if (lan == "fr") {
    Scritti = require("../json/par_fr.json");
  }

  Scritti.map((obj) => {
    obj.par_small = obj.par.toLowerCase();
  });

  const doSearch = async () => {
    var result = [];
    var listparwithword = [];
    result = Scritti;
    var myarraywords = text.split(" ");
    const queryres = myarraywords.map((word) => {
      result = result.filter((item) => item.par_small.includes(word));
    });
    setRespar(result);
    setArrayText(myarraywords);
  };

  const handleLetterDetail = (id) => {
    setLetDetail(id);
    handleShowModal();
  };

  const handleCloseModal = () => setShowModal(false);
  const handleShowModal = () => setShowModal(true);

  return (
    <>
      <Row>
        <Col>
          <div className="d-flex justify-content-between">
            <h1>{t("search_by_paragraph")}</h1>
            <div style={{ marginTop: 10 }}>
              <Form.Group
                className="mb-3"
                controlId="f1"
                style={{
                  marginTop: 0,
                  display: "inline-block",
                  marginRight: 10,
                }}
              >
                <Form.Control
                  name="search paragragh"
                  type="text"
                  value={text}
                  style={{ fontSize: 20, padding: 3 }}
                  required
                  placeholder={t("word_separated_space")}
                  onChange={(e) => setText(e.target.value)}
                  size="sm"
                />
              </Form.Group>
              <Button
                variant="default"
                style={{
                  marginRight: 10,
                  marginTop: -5,
                  fontSize: 18,
                  display: "inline-block",
                  backgroundColor: "#626556",
                  color: "white",
                }}
                onClick={() => doSearch()}
              >
                {t("search")}
              </Button>
            </div>
          </div>
        </Col>
      </Row>

      <Row>
        <Col>
          <SearchResults
            respar={respar}
            arrayText={arrayText}
            handleLetterDetail={handleLetterDetail}
          />
        </Col>
      </Row>

      <Modal size="lg" show={showModal} onHide={handleCloseModal}>
        <LetterDetail
          handleCloseModal={handleCloseModal}
          letDetail={letDetail}
        />
      </Modal>
    </>
  );
}

export default Search;
