import React, { useState, useEffect } from "react";

import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import i18n from "../languages/i18n";
import { useTranslation } from "react-i18next";

import Color from "../config/colors";
import Menu from "./Menu";
import Search from "./Search";
import SearchLetter from "./SearchLetter";
import SearchNumber from "./SearchNumber";
import Bio from "./Bio";
import Cloud from "./Cloud";
import LetterOrigin from "./LetterOrigin";
import Timeline from "./Timeline2";

function Home(props) {
  const { t, i18n } = useTranslation();
  const [activeScr, setAtiveScr] = useState([]);

  useEffect(() => {
    handleScr(0);
  }, []);

  const handleScr = (scr) => {
    var myarr = [];
    for (var i = 0; i < 7; i++) {
      myarr.push(false);
    }
    myarr[scr] = true;
    setAtiveScr(myarr);
  };

  return (
    <>
      <Menu handleScr={handleScr} />
      <Container>
        {activeScr[0] && <Search />}
        {activeScr[1] && <SearchLetter />}
        {activeScr[2] && <SearchNumber />}
        {activeScr[3] && <Bio />}
        {activeScr[4] && <Cloud />}
        {activeScr[5] && <LetterOrigin />}
        {activeScr[6] && <Timeline />}
      </Container>
    </>
  );
}

export default Home;
