import React, { useState, useEffect } from "react";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import Modal from "react-bootstrap/Modal";
import { useTranslation } from "react-i18next";

import SearchLetterResult from "./SearchLetterResult";
import LetterDetail from "./LetterDetail";

function SearchLetter() {
  const { t, i18n } = useTranslation();
  const [to, setTo] = useState("");
  const [subject, setSubject] = useState("");
  const [from, setFrom] = useState("");
  const [date, setDate] = useState("");
  const [respar, setRespar] = useState([]);
  const [showModal, setShowModal] = useState(false);
  const [letDetail, setLetDetail] = useState();

  const lan = i18n.language;

  const Letters = require("../json/letters.json");
  Letters.map((obj) => {
    obj.to_small = obj.to.toLowerCase();
    obj.from_small = obj.from.toLowerCase();
    obj.subject_small = obj.subject.toLowerCase();
  });

  var letterSmallCase = Letters;

  const doSearch = () => {
    var toclean = to.trim().toLowerCase();
    var result = letterSmallCase.filter((item) =>
      item.to_small.includes(toclean)
    );
    var fromclean = from.trim().toLowerCase();
    result = result.filter((item) => item.from_small.includes(fromclean));
    var subjectclean = subject.trim().toLowerCase();
    result = result.filter((item) => item.subject_small.includes(subjectclean));
    var dateclean = date.trim().toLowerCase();
    result = result.filter((item) => item.date.includes(dateclean));

    console.log(result);
    setRespar(result);
  };

  const cleanForm = () => {
    setTo("");
    setFrom("");
    setSubject("");
    setDate("");
    setRespar([]);
  };

  const handleLetterDetail = (id) => {
    setLetDetail(id);
    handleShowModal();
  };

  const handleCloseModal = () => setShowModal(false);
  const handleShowModal = () => setShowModal(true);

  return (
    <Row>
      <h1>{t("search_by_letter")}</h1>

      <Row>
        <Col>
          <Form.Group className="mb-3" controlId="f1">
            <Form.Label>Search by to:</Form.Label>
            <Form.Control
              name="person"
              type="text"
              value={to}
              required
              placeholder="person"
              onChange={(e) => setTo(e.target.value)}
              size="sm"
            />
          </Form.Group>
        </Col>
        <Col>
          <Form.Group className="mb-3" controlId="f1">
            <Form.Label>Search by from:</Form.Label>
            <Form.Control
              name="from"
              type="text"
              value={from}
              required
              placeholder="from"
              onChange={(e) => setFrom(e.target.value)}
              size="sm"
            />
          </Form.Group>
        </Col>
        <Col>
          <Form.Group className="mb-3" controlId="f1">
            <Form.Label>Search by subject:</Form.Label>
            <Form.Control
              name="object"
              type="text"
              value={subject}
              required
              placeholder="object"
              onChange={(e) => setSubject(e.target.value)}
              size="sm"
            />
          </Form.Group>
        </Col>
        <Col>
          <Form.Group className="mb-3" controlId="f1">
            <Form.Label>Search by year</Form.Label>
            <Form.Control
              name="date"
              type="text"
              value={date}
              required
              placeholder="year"
              onChange={(e) => setDate(e.target.value)}
              size="sm"
            />
          </Form.Group>
        </Col>
      </Row>
      <Row>
        <Col>
          <div className="d-flex justify-content-between">
            <Button
              variant="default"
              style={{
                marginRight: 10,
                marginTop: -5,
                fontSize: 18,
                display: "inline-block",
                backgroundColor: "#626556",
                color: "white",
              }}
              onClick={() => doSearch()}
            >
              Search
            </Button>
            <Button
              variant="default"
              style={{
                marginRight: 10,
                marginTop: -5,
                fontSize: 18,
                display: "inline-block",
                backgroundColor: "#626556",
                color: "white",
              }}
              onClick={() => cleanForm()}
            >
              Clean form
            </Button>
          </div>
        </Col>
      </Row>
      <Row>
        <Col>
          <SearchLetterResult
            respar={respar}
            handleLetterDetail={handleLetterDetail}
          />
        </Col>
      </Row>

      <Modal size="xl" show={showModal} onHide={handleCloseModal}>
        <LetterDetail
          handleCloseModal={handleCloseModal}
          letDetail={letDetail}
        />
      </Modal>
    </Row>
  );
}

export default SearchLetter;
