import React from "react";
import ReactWordcloud from "react-wordcloud";

import "tippy.js/dist/tippy.css";
import "tippy.js/animations/scale.css";

import words from "../languages/mycloud";

const options = {
  colors: [
    "#1f77b4",
    "#ff7f0e",
    "#2ca02c",
    "#d62728",
    "#9467bd",
    "#8c564b",
    "#335567",
    "#447722",
  ],
  enableTooltip: true,
  deterministic: false,
  fontFamily: "impact",
  fontSizes: [12, 68],
  fontStyle: "normal",
  fontWeight: "normal",
  padding: 1,
  rotations: 3,
  rotationAngles: [0, 90],
  scale: "sqrt",
  //spiral: "archimedean",
  transitionDuration: 1000,
};

function Cloud(props) {
  return (
    <div
      style={{
        height: "90vh",
        width: "95vw",
      }}
    >
      <ReactWordcloud words={words} options={options} maxWords={600} />
    </div>
  );
}

export default Cloud;
