import React, { useState } from "react";
import {
  MapContainer,
  TileLayer,
  Marker,
  Popup,
  Circle,
  CircleMarker,
} from "react-leaflet";
import { useTranslation } from "react-i18next";
import L from "leaflet";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Modal from "react-bootstrap/Modal";
import "leaflet/dist/leaflet.css";

import Meta from "../json/meta_data.json";
import Letters from "../json/letters.json";
import LetterDetail from "./LetterDetail";

function getIcon(w, h) {
  return L.icon({
    iconUrl: require("../assets/img/logoPLS.png"),
    iconSize: [w, h],
  });
}

function LetterOrigin(props) {
  const { t, i18n } = useTranslation();
  const [place, setPlace] = useState();
  const [showModal, setShowModal] = useState(false);
  const [letDetail, setLetDetail] = useState();
  var metalist = [];
  const getListMarkers = () => {
    Meta.map((place) => {
      if (place.name != "") {
        const countLetter = Letters.filter(
          (letter) => letter.fromlink === place.id
        ).length;
        metalist.push({
          id: place.id,
          name: place.name,
          coordinates: place.coordinates,
          count: countLetter,
        });
      }
    });
  };

  getListMarkers();

  const position = [51.505, -0.09];
  const center = [51.505, -0.09];

  const redOptions = { color: "red" };

  const handleClickMarkers = (id) => {
    console.log("you clicked place with id:", id);
    setPlace(id);
  };

  const handleLetterDetail = (id) => {
    setLetDetail(id);
    handleShowModal();
  };

  const handleCloseModal = () => setShowModal(false);
  const handleShowModal = () => setShowModal(true);

  return (
    <>
      <Row
        style={{
          marginTop: 10,
        }}
      >
        <Col sm={8}>
          <MapContainer
            center={[35, 20]}
            zoom={4}
            minZoom={4}
            maxZoom={12}
            style={{ width: "100%", height: "85vh", border: "black solid 1px" }}
          >
            <TileLayer
              attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
              url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
            />
            {/* <Marker position={markers[0].coor} icon={getIcon(50, 50)}>
            <Popup>{markers[0].text}</Popup>
          </Marker> */}
            {/* <Circle
            center={markers[0].coor}
            pathOptions={fillBlueOptions}
            radius={300}
          /> */}

            {metalist.map((place) => {
              return (
                <CircleMarker
                  key={place.id}
                  center={place.coordinates}
                  pathOptions={redOptions}
                  radius={5 + place.count / 15}
                  eventHandlers={{
                    click: (e) => {
                      handleClickMarkers(place.id);
                    },
                  }}
                >
                  <Popup>
                    {place.name} ({place.count})
                  </Popup>
                </CircleMarker>
              );
            })}
          </MapContainer>
        </Col>
        <Col sm={4} style={{ height: "85vh", overflowY: "scroll" }}>
          <Row>
            <Col align="left">
              {Letters.filter(
                (letter) => (letter.fromlink === place) & (letter.to != "")
              ).length > 0 ? (
                <h4 className="">
                  {t("results")}:&nbsp;
                  {
                    Letters.filter(
                      (letter) =>
                        (letter.fromlink === place) & (letter.to != "")
                    ).length
                  }
                </h4>
              ) : (
                <div></div>
              )}
            </Col>
          </Row>
          {Letters.filter(
            (letter) => (letter.fromlink === place) & (letter.to != "")
          ).map((filteredLetter) => (
            <div
              key={filteredLetter.id}
              className="letter_card"
              onClick={() => handleLetterDetail(filteredLetter.id)}
            >
              <div>{filteredLetter.to}</div>
              <div>
                {filteredLetter.from} - {filteredLetter.date}
              </div>
            </div>
          ))}
        </Col>
      </Row>
      <Modal size="xl" show={showModal} onHide={handleCloseModal}>
        <LetterDetail
          handleCloseModal={handleCloseModal}
          letDetail={letDetail}
        />
      </Modal>
    </>
  );
}

export default LetterOrigin;
